<template>
  <div class="notificacoes">
    <article v-for="notificacao in notificacoes" class="message" :key="notificacao.id"
      :class="contexto[notificacao.tipo]">
      <div class="message-header">
        {{ notificacao.titulo }}
      </div>
      <div class="message-body">
        {{ notificacao.texto }}
      </div>
    </article>
  </div>
</template>

<script lang="ts">
import { TipoDeNotificacao } from '@/interfaces/INotificacao';
import { useStore } from '@/store';
import { computed, defineComponent } from 'vue'

export default defineComponent({
  name: 'NotificacoesComponent',
  data() {
    return {
      contexto: {
        [TipoDeNotificacao.SUCESSO]: 'is-success',
        [TipoDeNotificacao.ATENCAO]: 'is-warning',
        [TipoDeNotificacao.FALHA]: 'is-danger',
      }
    }
  },
  setup() {
    const store = useStore()
    return {
      notificacoes: computed(() => store.state.notificacoes)
    }
  },
})
</script>

<style scoped>
.notificacoes {
  position: absolute;
  right: 0;
  width: 300px;
  padding: 8px;
  z-index: 10;
}
</style>